import axios from 'axios'
import {
  Code
} from './Code'
// import store from '../../store'
// axios 配置
// 访问超时时间
// axios.defaults.timeout = 5000
// 是否进行跨域控制
// axios.defaults.withCredentials = true
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=utf-8'
axios.defaults.baseURL = 'http://192.168.16.209:7777'
// 添加一个请求拦截器
// axios.interceptors.request.use(function (config) {
//   const token = store.getters.token
//   token && (config.headers['Authorization'] = token)
//   return config
// }, function (err) {
//   return Promise.reject({
//     code: Code.SysError,
//     data: err
//   })
// })
axios.interceptors.response.use(function (response) {
  if (response.status === 200 && response.data.code === Code.Success) {
    return response.data
  } else {
    let data = response.data || {}
    let err = Object.assign({ code: Code.SysError, msg: '操作失败！' }, data)
    err.errCode = err.code
    if (err.code === -101 || err.code === -102) {
      err.code = Code.Overdue
      err.msg = '用户信息已过期，请重新登录！'
    } else if (err.code === -104 || err.code === -106) {
      err.code = Code.NoAuth
      err.msg = '你无权限进行该操作，请切换用户后重试！'
    } else if (err.code === Code.Timeout) {
      err.code = Code.Timeout
    } else {
      err.code = Code.SysError
    }
    return Promise.reject(err)
    // throw err
  }
}, err => {
  if (err.message === 'Network Error') {
    return Promise.reject({
      code: Code.Timeout,
      msg: '网络连接超时，请稍后重试！'
    })
  } else {
    return Promise.reject({
      code: Code.SysError,
      msg: '系统错误，请稍后重试！'
    })
  }
})

function request(method, options) {
  options = Object.assign({
    method: method
  }, options)
  console.log(axios)
  return axios.request(options)
}

export const http = {
  get: (options) => {
    // options.params = options.data
    return request('get', options)
  },
  post: (options) => {
    return request('post', options)
  },
  put: (options) => {
    return request('put', options)
  },
  remove: (options) => {
    return request('delete', options)
  }
}



