<template>
  <div class="home">
    <div class="title">
      <el-row>
        <el-col :xs="0" :sm="2" :md="3" :lg="4" class="full-class">
        </el-col>
        <el-col :xs="24" :sm="20" :md="18" :lg="16">
          <el-breadcrumb separator=">">
            <span class="el-breadcrumb__item">当前位置：</span>
            <el-breadcrumb-item :to="{ path: '/' }">联盟</el-breadcrumb-item>
            <el-breadcrumb-item>加入联盟</el-breadcrumb-item>
            <el-breadcrumb-item>加入联盟信息</el-breadcrumb-item>
          </el-breadcrumb>
          <h3>当前时间：2017-09-05 星期三</h3>
        </el-col>
        <el-col :xs="0" :sm="2" :md="3" :lg="4" class="full-class">
        </el-col>
      </el-row>
    </div>
    <div class="box-park">
      <form id="parkFormId">
        <div class="box">
          <h1>平加入联盟申请</h1>
          <div class="park-row">
            <el-row>
              <el-col :xs="24" :sm="24" :md="6" :lg="6" class="row">
                <span>用户名： </span>
              </el-col>
              <el-col :xs="24" :sm="24" :md="18" :lg="18" class="row">
                <el-input autosize placeholder="请输入用户名"></el-input>
              </el-col>
            </el-row>
          </div>
          <div class="park-row">
            <el-row>
              <el-col :xs="24" :sm="24" :md="6" :lg="6" class="row">
                <span>用户类型： </span>
              </el-col>
              <el-col :xs="24" :sm="24" :md="18" :lg="18" class="row">
                <el-select v-model="form.type">
                  <el-option label="企业用户" value="company"></el-option>
                  <el-option label="个人用户" value="personal"></el-option>
                </el-select>
              </el-col>
            </el-row>
          </div>
          <div class="park-row">
            <el-row>
              <el-col :xs="24" :sm="24" :md="6" :lg="6" class="row">
                <span>单位/个人名次： </span>
              </el-col>
              <el-col :xs="24" :sm="24" :md="18" :lg="18" class="row">
                <el-input autosize placeholder="请输入内容"></el-input>
              </el-col>
            </el-row>
          </div>
          <div class="park-row">
            <el-row>
              <el-col :xs="24" :sm="24" :md="6" :lg="6" class="row">
                <span>所属国家： </span>
              </el-col>
              <el-col :xs="24" :sm="24" :md="18" :lg="18" class="row">
                <el-select v-model="form.country">
                  <el-option label="中国" value="china"></el-option>
                  <el-option label="美国" value="usa"></el-option>
                </el-select>
              </el-col>
            </el-row>
          </div>
          <div class="park-row">
            <el-row>
              <el-col :xs="24" :sm="24" :md="6" :lg="6" class="row">
                <span>联系人： </span>
              </el-col>
              <el-col :xs="24" :sm="24" :md="18" :lg="18" class="row">
                <el-input autosize placeholder="请输入内容"></el-input>
              </el-col>
            </el-row>
          </div>
          <div class="park-row">
            <el-row>
              <el-col :xs="24" :sm="24" :md="6" :lg="6" class="row">
                <span>联系电话： </span>
              </el-col>
              <el-col :xs="24" :sm="24" :md="18" :lg="18" class="row">
                <el-input type="number" autosize placeholder="请输入内容"></el-input>
              </el-col>
            </el-row>
          </div>
          <div class="park-row">
            <el-row>
              <el-col :xs="24" :sm="24" :md="6" :lg="6" class="row">
                <span>E-mail： </span>
              </el-col>
              <el-col :xs="24" :sm="24" :md="18" :lg="18" class="row">
                <el-input autosize placeholder="请输入内容"></el-input>
              </el-col>
            </el-row>
          </div>
          <div class="park-row">
            <el-row>
              <el-col :xs="24" :sm="24" :md="6" :lg="6" class="row">
                <span>入盟申请材料： </span>
              </el-col>
              <el-col :xs="24" :sm="24" :md="18" :lg="18" class="row">
                <el-upload
                  action="https://jsonplaceholder.typicode.com/posts/"
                  :on-preview="handlePreview"
                  :on-remove="handleRemove">
                  <el-button size="small" type="primary">上传</el-button>
                </el-upload>
              </el-col>
            </el-row>
          </div>
          <div class="park-row">
            <el-row>
              <el-col :xs="24" :sm="24" :md="6" :lg="6" class="row">
                <span>通讯地址： </span>
              </el-col>
              <el-col :xs="24" :sm="24" :md="18" :lg="18" class="row">
                <el-input autosize placeholder="请输入内容"></el-input>
              </el-col>
            </el-row>
          </div>
          <div class="park-row p-button">
            <el-row>
              <el-col :xs="12" :sm="12" :md="12" :lg="12" class="row">
                <el-button type="primary" size="large">申请加入</el-button>
              </el-col>
              <el-col :xs="12" :sm="12" :md="12" :lg="12" class="row">
                <el-button size="large" @click="resetForm">重置</el-button>
              </el-col>
            </el-row>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        form: {
          type: '',
          country: ''
        }
      }
    },
    methods: {
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
      resetForm(){
         document.getElementById('parkFormId').reset();
      }
    }
  }
</script>
<style lang="scss" scoped>
  .title{
    height: 72px;
    padding-top: 20px;
    background: #f2f9fc;
    font-size: 14px;
    line-height: 28px;
  }
  .title h3{
    font-weight: 400;
    font-size: 14px;
  }
  .el-breadcrumb{
    line-height: 28px;
    font-size: 14px;
  }
  .el-select{
    width: 100%;
  }
  .s-red{
    color: red;
  }
  .box-park{
    padding: 0.5rem 0;
  }
  .box-park .box{
     width: 50%;
     margin: 0 auto;
  }
  .box-park .box span{
     display: inline-block;
     font-size: 16px;
     color: #888;
  }
  .box-park .box span:first-child{
    text-align: right;
    vertical-align: 5px;
    width: 100%;
  }
  @media screen and (max-width: 992px) {
    .box-park .box{
      width: 90%;
    }
    .box-park .box span:first-child{
      text-align: left;
    }
    .box-park{
      padding: 0.2rem 0;
    }
  }
  .box-park .box .park-row{
     margin: 0.2rem 0;
  }
  .box-park h1{
     font-size: 16px;
     line-height: 30px;
     font-weight: 600;
     text-align: center;
  }
  .box-park button{
    display: block;
    margin: 0 auto;
    padding-left: 0.3rem;
    padding-right: 0.3rem;
  }
  .box-park button.el-button--primary{
    background-color: #5b8fed;
  }
  .box-park button:hover{
    filter: brightness(1.1) contrast(110%);
    -webkit-filter: brightness(1.1) contrast(110%);
  }
  .park-logo{
     width: 1rem;
     height: 1rem;
  }
  .upload-logo{
  }
  .p-button{
     margin-top: 0.5rem !important;
  }
  @media screen and (max-width: 737px){
    .title{
      padding-left: 15px;
      padding-right: 15px;
    }
  }
  @media screen and (max-width: 500px) {
    .box-park .box .park-row{
      margin: 0.1rem 0;
    }
    .upload-logo{
      margin: 0.2rem auto 0 auto;
    }
    .p-button{
      margin-top: 0.3rem !important;
    }
  }
</style>




