<template>
  <div class="home">
    <div class="box-park">
      <form id="parkFormId">
        <div class="box">
          <h1>平台接入申请</h1>
          <div class="park-row">
            <el-row>
              <el-col :xs="24" :sm="24" :md="6" :lg="6" class="row">
                <span>企业名称： </span>
              </el-col>
              <el-col :xs="24" :sm="24" :md="18" :lg="18" class="row">
                <el-input autosize placeholder="请输入内容"></el-input>
              </el-col>
            </el-row>
          </div>
          <div class="park-row">
            <el-row>
              <el-col :xs="24" :sm="24" :md="6" :lg="6" class="row">
                <span><i class="s-red">*</i>平台名称： </span>
              </el-col>
              <el-col :xs="24" :sm="24" :md="18" :lg="18" class="row">
                <el-input autosize placeholder="请输入内容"></el-input>
              </el-col>
            </el-row>
          </div>
          <div class="park-row">
            <el-row>
              <el-col :xs="24" :sm="24" :md="6" :lg="6" class="row">
                <span><i class="s-red">*</i>平台网址： </span>
              </el-col>
              <el-col :xs="24" :sm="24" :md="18" :lg="18" class="row">
                <el-input autosize placeholder="请输入内容"></el-input>
              </el-col>
            </el-row>
          </div>
          <div class="park-row">
            <el-row>
              <el-col :xs="24" :sm="24" :md="6" :lg="6" class="row">
                <span>平台介绍： </span>
              </el-col>
              <el-col :xs="24" :sm="24" :md="18" :lg="18" class="row">
                <el-input type="textarea" :autosize="{ minRows: 2, maxRows: 4}"  placeholder="请输入内容"></el-input>
              </el-col>
            </el-row>
          </div>
          <div class="park-row">
            <el-row>
              <el-col :xs="24" :sm="24" :md="6" :lg="6" class="row">
                <span><i class="s-red">*</i>平台申请人： </span>
              </el-col>
              <el-col :xs="24" :sm="24" :md="18" :lg="18" class="row">
                <el-input autosize placeholder="请输入内容"></el-input>
              </el-col>
            </el-row>
          </div>
          <div class="park-row">
            <el-row>
              <el-col :xs="24" :sm="24" :md="6" :lg="6" class="row">
                <span><i class="s-red">*</i>申请人手机号码： </span>
              </el-col>
              <el-col :xs="24" :sm="24" :md="18" :lg="18" class="row">
                <el-input type="number" autosize placeholder="请输入内容"></el-input>
              </el-col>
            </el-row>
          </div>
          <div class="park-row">
            <el-row>
              <el-col :xs="24" :sm="24" :md="6" :lg="6" class="row">
                <span>E-mail： </span>
              </el-col>
              <el-col :xs="24" :sm="24" :md="18" :lg="18" class="row">
                <el-input autosize placeholder="请输入内容"></el-input>
              </el-col>
            </el-row>
          </div>
          <div class="park-row">
            <el-row>
              <el-col :xs="24" :sm="24" :md="6" :lg="6" class="row">
                <span>平台logo： </span>
              </el-col>
              <el-col :xs="24" :sm="24" :md="18" :lg="18" class="row">
                <img class="park-logo" src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1506672889&di=d6e4350085d34d8ad011b46552cdf3f6&imgtype=jpg&er=1&src=http%3A%2F%2Fww2.sinaimg.cn%2Fcrop.0.0.998.998.1024%2Fbf4ff4f8jw1eljxdwibc8j20rs0rsn1b.jpg">
                <el-upload
                  class="upload-logo"
                  action="https://jsonplaceholder.typicode.com/posts/"
                  :on-preview="handlePreview"
                  :on-remove="handleRemove">
                  <el-button size="small" type="primary">点击上传</el-button>
                  <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                </el-upload>
              </el-col>
            </el-row>
          </div>
          <div class="park-row p-button">
            <el-row>
              <el-col :xs="12" :sm="12" :md="12" :lg="12" class="row">
                <el-button type="primary" size="large">提交</el-button>
              </el-col>
              <el-col :xs="12" :sm="12" :md="12" :lg="12" class="row">
                <el-button size="large" @click="resetForm">重置</el-button>
              </el-col>
            </el-row>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {}
    },
    methods: {
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
      resetForm(){
         document.getElementById('parkFormId').reset();
      }
    }
  }
</script>
<style lang="scss" scoped>
  .home{
    background: #f2f9fc;
  }
  .s-red{
    color: red;
  }
  .box-park{
    padding: 0.5rem 0;
  }
  .box-park .box{
     width: 50%;
     margin: 0 auto;
  }
  .box-park .box span{
     display: inline-block;
     font-size: 16px;
     color: #888;
  }
  .box-park .box span:first-child{
    text-align: right;
    vertical-align: 5px;
    width: 100%;
  }
  @media screen and (max-width: 992px) {
    .box-park .box{
      width: 90%;
    }
    .box-park .box span:first-child{
      text-align: left;
    }
    .box-park{
      padding: 0.2rem 0;
    }
  }
  .box-park .box .park-row{
     margin: 0.2rem 0;
  }
  .box-park h1{
     font-size: 16px;
     line-height: 30px;
     font-weight: 600;
     text-align: center;
  }
  .box-park button{
    display: block;
    margin: 0 auto;
    padding-left: 0.3rem;
    padding-right: 0.3rem;
  }
  .box-park button.el-button--primary{
    background-color: #5b8fed;
  }
  .box-park button:hover{
    filter: brightness(1.1) contrast(110%);
    -webkit-filter: brightness(1.1) contrast(110%);
  }
  .park-logo{
     width: 1rem;
     height: 1rem;
  }
  .upload-logo{
     display: inline-block;
     margin-top: 0.24rem;
     margin-left: 0.3rem;
  }
  .p-button{
     margin-top: 0.5rem !important;
  }
  @media screen and (max-width: 500px) {
    .box-park .box .park-row{
      margin: 0.1rem 0;
    }
    .upload-logo{
      margin: 0.2rem auto 0 auto;
    }
    .p-button{
      margin-top: 0.3rem !important;
    }
  }
</style>




