<template>
  <div class="home">
    <div class="swip">
      <el-carousel trigger="click" height="38vw">
        <el-carousel-item v-for="item in banners" :key="item">
          <img class="img" :src="item" alt="">
        </el-carousel-item>
      </el-carousel>
    </div>
    <div class="box-park">
      <router-link :to="{ path: 'plat-insert'}">
        <el-button type="primary" size="large">
          <i class="iconfont icon-pingtaifangan"></i>
          平台接入申请
        </el-button>
      </router-link>
    </div>
    <div class="box-park">
      <div class="box">
        <h1>尊敬的用户：</h1>
        <div>感谢您注册一带一路数字丝绸之路公共服务平台！并接入专业平台（系统）。我们将为您提供双方平台之间的用户和资源共享，拓展您的合作范围，加强您企业的品牌推广。目前“示范平台”栏目尚在建设中，能为您提供专业平台（系统）接入申请，后续将有服务人员联系您平台接入事宜。</div>
        <span class="sub">平台运营中心</span>
        <span>2017年11月</span>
      </div>
    </div>
    <div class="box-park bg">
      <div class="box">
        <h1 class="title">合作平台
          <a v-if="platformList!==platformListData" @click="morePlat()">more</a>
        </h1>
        <el-row :gutter="20">
          <el-col :xs="12" :sm="12" :md="8" :lg="6" class="row" v-for="plat in platformList">
            <a class="grid-content" @click="goLink(plat)">
              <img :src="plat.logo" :alt="plat.title">
            </a>
          </el-col>
        </el-row>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      banners: [
        'http://img2.3lian.com/2014/f4/152/d/14.jpg',
        'http://img1.gamedog.cn/2014/05/08/119-14050P950330.jpg',
        'http://meitu.qqzong.com/uploads/allimg/150131/014254CA-0.jpg'
      ],
      platformList: [],
      platformListData: []
    }
  },
  created: function () {
    this.queryList()
  },
  methods: {
    queryList() {
      this.platformListData = [
        {
          title: '百度', url: 'www.baidu.com', logo: 'http://img.jiaodong.net/pic/0/12/58/34/12583482_645175.jpg'
        },
        {
          title: '百度', url: 'www.baidu.com', logo: 'http://pic2.ooopic.com/11/84/76/77bOOOPIC08.jpg'
        },
        {
          title: '百度', url: 'www.baidu.com', logo: 'http://pic.58pic.com/58pic/15/41/09/50E58PICQ6e_1024.jpg'
        },
        {
          title: '百度', url: 'www.baidu.com', logo: 'http://pic.qiantucdn.com/58pic/17/88/18/25F58PICYxR_1024.jpg'
        },
        {
          title: '百度', url: 'www.baidu.com', logo: 'http://pic.qiantucdn.com/58pic/18/14/11/14Y58PICIdx_1024.jpg'
        },
        {
          title: '百度', url: 'www.baidu.com', logo: 'http://img.lagou.com/thumbnail_300x300/i/image/M00/1C/2A/CgqKkVb_1JaAAVERAACW7PPjubs664.png'
        }
      ]
      this.platformList = this.platformListData.slice(0, 4)
    },
    morePlat() {
      this.platformList = this.platformListData
    },
    goLink(plat) {
      window.open('https://' + plat.url)
    }
  }
}
</script>
<style lang="scss" scoped>
.home {
  background: #f2f9fc;
}

.swip {
  img {
    width: 100%;
  }
}

.box-park {
  padding: 0.1rem 0;
}

.box-park button {
  display: block;
  margin: 0 auto;
  padding-left: 0.3rem;
  padding-right: 0.3rem;
  background-color: #5b8fed;
}

.box-park button:hover {
  filter: brightness(1.1) contrast(110%);
  -webkit-filter: brightness(1.1) contrast(110%);
}

.box-park i.icon-pingtaifangan {
  margin-right: 0.04rem;
  font-size: 0.8em;
  vertical-align: middle;
}

.bg {
  background-color: #fff;
}

.box-park .box {
  width: 80%;
  margin: 0 auto 0.5rem auto;
  color: #666;
  line-height: 24px;
  font-size: 14px;
}

@media screen and (max-width: 500px) {
  .box-park .box {
    width: 90%;
    margin-bottom: 0;
    margin-top: 0;
  }
}

.box-park .box h1 {
  margin-top: 0.1rem;
  font-size: 16px;
  font-weight: 800;
  line-height: 30px;
}

.box-park .box h1.title:before {
  content: ' ';
  display: inline-block;
  width: 4px;
  height: 18px;
  background-color: #5a89df;
  vertical-align: middle;
  margin-right: 0.04rem;
}

.box-park .box h1.title a {
  float: right;
  font-weight: 400;
}

.box-park .box h1.title a:hover {
  color: #5b8fed;
}

.box-park .box span {
  display: inline-block;
  width: 100%;
  text-align: right;
}

.box-park .box span.sub {
  margin-top: 0.1rem;
}

.box-park .box button {
  margin-top: 0.2rem;
}

.row {
  margin-top: 20px;
}

.grid-content {
  display: block;
  position: relative;
  border-radius: 4px;
  min-height: 36px;
  height: 2rem;
  box-shadow: 0 1px 2px 1px #ccc;
  overflow: hidden;
}

@media screen and (max-width: 500px) {
  .grid-content {
    height: 25vw;
  }
  .box-park .box h1 {
    margin-top: 0;
  }
}

.grid-content img {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  height: 84%;
  display: block;
  transition: filter .3s, -webkit-filter .3s;
}

.grid-content img:hover {
  filter: brightness(1.2) contrast(120%);
  -webkit-filter: brightness(1.2) contrast(120%);
}
</style>




