<template>
  <!-- 未添加分页 -->
  <div class="wrapper">
    <div class="header">
      <div class="inner">
        <div class="breadcrumb-cotainer">
          <el-breadcrumb separator=">" class="bread-color">
            <el-breadcrumb-item>合作</el-breadcrumb-item>
            <el-breadcrumb-item>全部服务</el-breadcrumb-item>
          </el-breadcrumb>
        </div>
        <div class="btn-wrap">
          <el-button type="primary" class="publish">
            <i class="iconfont icon-xinghaoqi"></i> 发布服务</el-button>
        </div>
        <div class="form-wrap">
          <div class="form-control">
            <el-select v-model="value1" placeholder="城市">
              <el-option v-for="item in options1" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </div>
          <div class="form-control">
            <el-select v-model="value2" placeholder="类别">
              <el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </div>
          <div class="search">
            <el-input placeholder="请输入关键字" icon="search" v-model="input2" :on-icon-click="handleIconClick">
            </el-input>
          </div>
        </div>
      </div>
    </div>
    <div class="content">
      <div class="inner">
        <div class="service-card" v-for="item in cardList" :key="item.title">
          <div class="product-logo">
            <img src="../../../assets/image/demand-item.png" alt="">
          </div>
          <div style="flex:10;min-width:122px;"></div>
          <div class="title">
            <h4>{{item.title}}</h4>
            <p>服务范围介绍服务范围介绍服务范围介绍服务范围介绍服务范围介绍服务范围介绍服务范围介绍服务范围介绍服务范围介绍服务范围介绍服务范围介绍服务范围介绍服务范围介绍服务范围介绍服务范围介绍服务范围介绍服务范围介绍服务范围介绍</p>
          </div>
          <div class="wedge">
            <div class="wedge-content">电子产品</div>
          </div>
          <div class="info">
            <span>{{item.date}}</span>
            <span class="number">面议</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      options1: [
        { value: '1', label: '黑龙江' },
        { value: '1', label: '吉林' },
        { value: '1', label: '辽宁' }
      ],
      options2: [
        { value: '1', label: '实打实的' },
        { value: '1', label: '是多少' },
        { value: '1', label: '是多少啊' }
      ],
      input2: '',
      value1: '',
      value2: '',
      cardList: [
        { title: '路灯新产品', date: '2017-08-25' },
        { title: '路灯新产品', date: '2017-08-25' },
        { title: '路灯新产品', date: '2017-08-25' },
        { title: '路灯新产品', date: '2017-08-25' },
        { title: '阿萨德', date: '2017-08-25' },
        { title: '路灯新产品', date: '2017-08-25' },
        { title: '就有人', date: '2017-08-25' },
        { title: '路灯新产品', date: '2017-08-25' },
        { title: '的饭就已经大丰港是大法官是', date: '2017-08-25' },
        { title: '路灯新产品', date: '2017-08-25' }
      ]
    }
  }
}
</script>
<style lang="scss" scoped>
.wrapper {
  width: 100%;
  .inner {
    width: 94%;
    max-width: 1280px;
    margin: auto;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
  .header {
    width: 100%;
    line-height: 90px;
    background: linear-gradient(top, #c7d5e1, #f2f9fc);
    font-size: 16px;
    .bread-color {
      padding-left: 10px;
      height: 100%;
      min-width: 130px;
      line-height: 90px;
      flex: 1;
    }
    .btn-wrap {
      flex: 2;
      display: flex;
      justify-content: flex-end;
      .publish {
        width: 20vw;
        min-width: 120px;
        height: 45px;
        background: #f95e4c;
        color: #fff;
        i {
          margin-right: 5px;
        }
      }
    }
    .form-wrap {
      flex: 3;
      display: flex;
      justify-content: flex-end;
      min-width: 52vw;
      .form-control {
        width: 100px;
        display: inline-block;
        margin-right: 0.8vw;
      }
      .search {
        width: 200px;
        display: inline-block;
        margin-right: 4vw;
      }
    }
  }
  .content {
    padding-top: 80px;
    .service-card {
      width: 100%;
      height: 134px;
      position: relative;
      box-shadow: 2px 3px 12px 5px #e2eaff;
      margin: 0 auto 50px;
      display: flex;
      .product-logo {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 112px;
        height: 112px;
        img {
          width: 100%;
          height: 100%;
        }
        &:before {
          content: '';
          position: absolute;
          width: 0;
          height: 0;
          border: 16px solid transparent;
          border-top-color: #fff;
          transform: rotate(-135deg);
          top: -16px;
          right: -16px;
        }
      }
      .title {
        flex: 34;
        padding-top: 16px;
        h4 {
          font-weight: bold;
          font-size: 18px;
          line-height: 30px;
        }
        p {
          height: 65px;
          font-size: 14px;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 3;
        }
      }
      .wedge {
        flex: 15;
        display: flex;
        align-items: center;
        display: flex;
        justify-content: center;
        .wedge-content {
          width: 80px;
          height: 32px;
          background: #5a89df;
          color: #fff;
          position: relative;
          text-align: center;
          line-height: 32px;
          &:before,
          &:after {
            content: '';
            width: 0;
            height: 0;
            border: 16px solid transparent;
            border-left-width: 8px;
            border-right-width: 8px;
            position: absolute;
            top: 0;
          }
          &:before {
            border-left-color: #fff;
            left: 0;
          }
          &:after {
            border-left-color: #5a89df;
            right: -16px;
          }
        }
      }
      .info {
        flex: 11;
        display: flex;
        flex-direction: column;
        line-height: 67px;
        text-align: center;
        .number {
          color: #fb453e;
          font-size: 18px;
        }
      }
    }
  }
}

@media screen and (max-width: 720px) {
  .wrapper {
    .header {
      .inner {
        .btn-wrap {
          justify-content: center;
          button {
            width: 50vw;
          }
        }
      }
    }
    .content {
      .service-card {
        .wedge {
          display: none;
        }
      }
    }
  }
}

@media screen and (max-width: 520px) {
  .wrapper {
    .content {
      .service-card {
        .title {
          flex: 80;
        }
        .info {
          display: none;
        }
      }
    }
  }
}
</style>




