<template>
  <div>
    <div class="wrapper">
      <div class="header">
        <div class="inner">
          <div class="breadcrumb-cotainer">
            <el-breadcrumb separator=">" class="bread-color">
              <el-breadcrumb-item>发现</el-breadcrumb-item>
              <el-breadcrumb-item>行业活动</el-breadcrumb-item>
              <el-breadcrumb-item>全部活动</el-breadcrumb-item>
            </el-breadcrumb>
          </div>
          <div class="btn-wrap">
            <el-button type="primary" class="publish">
              <i class="iconfont icon-xinghaoqi"></i> 发布活动</el-button>
          </div>
          <div class="form-wrap">
            <div class="form-control">
              <el-select v-model="value1" placeholder="城市">
                <el-option v-for="item in options1" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </div>
            <div class="form-control">
              <el-select v-model="value2" placeholder="类别">
                <el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </div>
            <div class="search">
              <el-input placeholder="请输入关键字" icon="search" v-model="input2" :on-icon-click="handleIconClick">
              </el-input>
            </div>
          </div>
        </div>
      </div>
      <div class="content">
        <div class="content-inner">
          <div class="activity-card" v-for="item in cardList" :key="item.title">
            <img class="product-logo" src="../../../assets/image/activity-card.jpg" alt="">
            <div class="title">
              <h4>{{item.title}}</h4>
              <span>园区:经济开发区</span>
              <p>园区入驻服务，也为平台注册的园区机构提供园区招商、园区合作等方面的多项服务，助力园区在经济增长、企业孵化、人才培育</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      options1: [
        { value: '1', label: '黑龙江' },
        { value: '1', label: '吉林' },
        { value: '1', label: '辽宁' }
      ],
      options2: [
        { value: '1', label: '实打实的' },
        { value: '1', label: '是多少' },
        { value: '1', label: '是多少啊' }
      ],
      input2: '',
      value1: '',
      value2: '',
      cardList: [
        { title: '路灯新产品', date: '2017-08-25' },
        { title: '路灯新产品', date: '2017-08-25' },
        { title: '路灯新产品', date: '2017-08-25' },
        { title: '路灯新产品', date: '2017-08-25' },
        { title: '阿萨德', date: '2017-08-25' },
        { title: '路灯新产品', date: '2017-08-25' },
        { title: '就有人', date: '2017-08-25' },
        { title: '路灯新产品', date: '2017-08-25' },
        { title: '的饭就已经大丰港是大法官是', date: '2017-08-25' },
        { title: '路灯新产品', date: '2017-08-25' }
      ]
    }
  },
  methods: {
    handleIconClick() { }
  }
}
</script>
<style lang="scss" scoped>
.wrapper {
  width: 100%;
  .inner {
    width: 94%;
    max-width: 1280px;
    margin: auto;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
  .content-inner {
    width: 94%;
    max-width: 1280px;
    margin: auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .header {
    width: 100%;
    line-height: 90px;
    background: linear-gradient(top, #c7d5e1, #f2f9fc);
    font-size: 16px;
    .bread-color {
      padding-left: 10px;
      height: 100%;
      min-width: 130px;
      line-height: 90px;
      flex: 1;
    }
    .btn-wrap {
      flex: 2;
      display: flex;
      justify-content: flex-end;
      .publish {
        width: 20vw;
        min-width: 120px;
        height: 45px;
        background: #f95e4c;
        color: #fff;
        i {
          margin-right: 5px;
        }
      }
    }
    .form-wrap {
      flex: 3;
      display: flex;
      justify-content: flex-end;
      min-width: 52vw;
      .form-control {
        width: 100px;
        display: inline-block;
        margin-right: 0.8vw;
      }
      .search {
        width: 200px;
        display: inline-block;
        margin-right: 4vw;
      }
    }
  }
  .content {
    padding-top: 80px;
    .activity-card {
      width: 48%;
      height: 16.4vw;
      min-height: 134px;
      overflow: hidden;
      position: relative;
      box-shadow: 2px 3px 12px 5px #e2eaff;
      margin: 0 auto 50px;
      display: flex;
      .product-logo {
        width: 20vw;
        height: 16.4vw;
        min-width: 136px;
        min-height: 134px;
      }
      .title {
        flex: 34;
        padding-left: 26px;
        h4 {
          font-weight: bold;
          font-size: 18px;
          line-height: 45px;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          width: 96%;
        }
        span {
          font-size: 14px;
          line-height: 30px;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        p {
          height: calc(16.4vw - 75px);
          line-height: 30px;
          min-height: 60px;
          max-height: 88px;
          padding: 0 10px 10px 0;
          font-size: 14px;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
    }
  }
}

@media screen and (max-width: 980px) {
  .wrapper {
    .header {
      .inner {
        .btn-wrap {
          justify-content: center;
          button {
            width: 50vw;
          }
        }
      }
    }
    .content {
      .content-inner {
        .activity-card {
          width: 100%;
        }
      }
    }
  }
}

@media screen and (max-width: 520px) {
  .wrapper {
    .content {
      .activity-card {
        .title {
          flex: 80;
        }
        .info {
          display: none;
        }
      }
    }
  }
}
</style>



