<template>
  <!-- 分页没有做 -->
  <div class="wrapper">
    <div class="header">
      <div class="inner">
        <div class="breadcrumb-cotainer">
          <el-breadcrumb separator=">" class="bread-color">
            <el-breadcrumb-item>合作</el-breadcrumb-item>
            <el-breadcrumb-item>全部需求</el-breadcrumb-item>
          </el-breadcrumb>
        </div>
        <div class="btn-wrap">
          <el-button type="primary" class="publish">
            <i class="iconfont icon-xuqiu1"></i> 发布需求</el-button>
        </div>
        <div class="form-wrap">
          <div class="form-control">
            <el-select v-model="value1" placeholder="城市">
              <el-option v-for="item in options1" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </div>
          <div class="form-control">
            <el-select v-model="value2" placeholder="类别">
              <el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </div>
          <div class="search">
            <el-input placeholder="请输入关键字" icon="search" v-model="input2" :on-icon-click="handleIconClick">
            </el-input>
          </div>
        </div>
      </div>
    </div>
    <div class="content">
      <div class="inner">
        <div class="demand-card" v-for="item in cardList" :key="item.title">
          <div class="product-logo">
            <img src="../../../assets/image/demand-item.png" alt="">
          </div>
          <div class="flag">合作</div>
          <div style="flex:20;min-width:132px;"></div>
          <div class="title">{{item.title}}</div>
          <div class="wedge">
            <div class="wedge-content">电子产品</div>
          </div>
          <div class="info">
            <span>2017-08-25</span>
            <span>中国，上海</span>
          </div>
          <div class="info">
            <span>30人浏览过</span>
            <span class="number">{{item.money}}万</span>
          </div>
        </div>
        <div class="paging">
          <el-pagination @current-change="pageChange" :current-page="pager.index" :page-size="pager.num" layout="total, sizes, prev, pager, next, jumper" :total="pager.total">
          </el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Demand as api } from '../../../api'
export default {
  data() {
    return {
      pager: {
        index: 1,
        num: 10,
        total: 0
      },
      options1: [
        { value: '1', label: '黑龙江' },
        { value: '1', label: '吉林' },
        { value: '1', label: '辽宁' }
      ],
      options2: [
        { value: '1', label: '实打实的' },
        { value: '1', label: '是多少' },
        { value: '1', label: '是多少啊' }
      ],
      input2: '',
      value1: '',
      value2: '',
      cardList: [
        { title: '路灯新产品', money: '0' },
        { title: '路灯新产品', money: '0' },
        { title: '路灯新产品', money: '0' },
        { title: '路灯新产品', money: '0' },
        { title: '阿萨德', money: '30' },
        { title: '路灯新产品', money: '0' },
        { title: '就有人', money: '20' },
        { title: '路灯新产品', money: '0' },
        { title: '的饭就已经大丰港是大法官是', money: '0' },
        { title: '路灯新产品', money: '0' }
      ]
    }
  },
  methods: {
    handleIconClick() {

    },
    pageChange() {

    }
  },
  activated() {
    api.demandList().then(data => {
      console.log(data)
    })
  }
}
</script>
<style lang="scss" scoped>
.wrapper {
  width: 100%;
  .inner {
    width: 94%;
    max-width: 1280px;
    margin: auto;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
  .header {
    width: 100%;
    line-height: 90px;
    background: linear-gradient(top, #c7d5e1, #f2f9fc);
    font-size: 16px;
    .bread-color {
      padding-left: 10px;
      height: 100%;
      min-width: 130px;
      line-height: 90px;
      flex: 1;
    }
    .btn-wrap {
      flex: 2;
      display: flex;
      justify-content: flex-end;
      .publish {
        width: 20vw;
        min-width: 120px;
        height: 45px;
        background: #f95e4c;
        color: #fff;
        i {
          margin-right: 5px;
        }
      }
    }
    .form-wrap {
      flex: 3;
      display: flex;
      justify-content: flex-end;
      min-width: 52vw;
      .form-control {
        width: 100px;
        display: inline-block;
        margin-right: 0.8vw;
      }
      .search {
        width: 200px;
        display: inline-block;
      }
    }
  }
  .content {
    padding-top: 80px;
    .demand-card {
      width: 100%;
      height: 134px;
      position: relative;
      box-shadow: 2px 3px 12px 5px #e2eaff;
      margin: 0 auto 50px;
      display: flex;
      .product-logo {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 112px;
        height: 112px;
        img {
          width: 100%;
          height: 100%;
        }
        &:before {
          content: '';
          position: absolute;
          width: 0;
          height: 0;
          border: 16px solid transparent;
          border-top-color: #fff;
          transform: rotate(-135deg);
          top: -16px;
          right: -16px;
        }
      }
      .flag {
        background: #0ec1a6;
        color: #fff;
        width: 40px;
        height: 54px;
        line-height: 54px;
        text-align: right;
        position: absolute;
        bottom: 0;
        left: -5px;
        &:before {
          content: '';
          width: 0;
          height: 0;
          border: 4px solid transparent;
          border-top-color: #167f6f;
          transform: rotate(-45deg);
          position: absolute;
          left: 1px;
          top: -4px;
        }
        &:after {
          content: '';
          width: 0;
          height: 0;
          border: 12px solid transparent;
          border-left-color: #0ec1a6;
          border-top-width: 27px;
          border-bottom-width: 27px;
          position: absolute;
          top: 0;
          right: -24px;
        }
      }
      .title {
        flex: 34;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        font-weight: bold;
        font-size: 20px;
        line-height: 134px;
      }
      .wedge {
        flex: 15;
        display: flex;
        align-items: center;
        .wedge-content {
          width: 80px;
          height: 32px;
          background: #5a89df;
          color: #fff;
          position: relative;
          text-align: center;
          line-height: 32px;
          padding-left: 8px;
          &:before,
          &:after {
            content: '';
            width: 0;
            height: 0;
            border: 16px solid transparent;
            border-left-width: 8px;
            border-right-width: 8px;
            position: absolute;
            top: 0;
          }
          &:before {
            border-left-color: #fff;
            left: 0;
          }
          &:after {
            border-left-color: #5a89df;
            right: -16px;
          }
        }
      }
      .info {
        flex: 11;
        display: flex;
        flex-direction: column;
        line-height: 67px;
        .number {
          color: #fb453e;
          font-size: 34px;
        }
      }
    }
    .paging {
      width: 100%;
      height: 40px;
      margin-bottom: 30px;
      text-align: right;
    }
  }
}

@media screen and (max-width: 720px) {
  .wrapper {
    .header {
      .inner {
        .btn-wrap {
          justify-content: center;
          button {
            width: 50vw;
          }
        }
      }
    }
    .content {
      .demand-card {
        .wedge {
          display: none;
        }
      }
    }
  }
}

@media screen and (max-width: 520px) {
  .wrapper {
    .content {
      .demand-card {
        .title {
          flex: 80;
        }
        .info {
          display: none;
        }
      }
    }
  }
}
</style>




