import Vue from 'vue'
import Router from 'vue-router'
import Entrance from '@/views/entrance/entrance'
import Layout from '@/views/layout'
import Home from '@/views/home/index'
import requirement from '@/views/cooperation/requirement'
import service from '@/views/cooperation/service'
import PublishRequirements from '@/views/cooperation/publishRequirements'
import demandList from '@/views/cooperation/demand/list'
import demandDetail from '@/views/cooperation/demand/detail'
import serviceList from '@/views/cooperation/service/list'
import serviceDetail from '@/views/cooperation/service/detail'
import activityList from '@/views/cooperation/activity/list'
import activityDetail from '@/views/cooperation/activity/detail'
import ServiceOrganization from '@/views/serviceOrganization/index'
import expert from '@/views/serviceOrganization/expert'
import IndustrialPark from '@/views/industrialPark/index'
import IndustryActivity from '@/views/industryActivity/index'
import IndustryInfo from '@/views/industryInfo/index'
import Park from '@/views/example/park'
import ParkModel from '@/views/example/park-model'
import PlatInsert from '@/views/example/park-plat-insert'
import Idynamics from '@/views/news/Idynamics'
import regulations from '@/views/news/regulations'
import UnionLayout from '@/views/union/unionlayout'
import UnionHome from '@/views/union/unionhome'
import JoinUnion from '@/views/union/joinUnion'
import BusinessList from '@/views/garden/businessList'
import TerraceList from '@/views/garden/terraceList'
import GardenList from '@/views/garden/gardenList'
import GardenListDetails from '@/views/garden/gardenListDetails'
import SpecialList from '@/views/garden/specialList'
import SpecialListDetails from '@/views/garden/specialListDetails'

Vue.use(Router)
let router = new Router({
  linkActiveClass: 'active',
  routes: [{
    path: '/',
    name: 'entrance',
    component: Entrance
    },
    {
    path: '/site',
    name: 'layout',
    component: Layout,
    children: [
      { path: 'home', name: 'home', component: Home },
      { path: 'requirement', name: 'requirement', component: requirement },
      { path: 'service', name: 'service', component: service },
      { path: 'publishRequirements', name: 'publishRequirements', component: PublishRequirements },
      { path: 'demandList', name: 'demandList', component: demandList },
      { path: 'demandDetail', name: 'demandDetail', component: demandDetail },
      { path: 'serviceList', name: 'serviceList', component: serviceList },
      { path: 'serviceDetail', name: 'serviceDetail', component: serviceDetail },
      { path: 'activityList', name: 'activityList', component: activityList },
      { path: 'activityDetail', name: 'activityDetail', component: activityDetail },
      { path: 'industrialPark', name: 'industrialPark', component: IndustrialPark },
      { path: 'park', component: Park },
      { path: 'parkModel', component: ParkModel },
      { path: 'plat-insert', component: PlatInsert },
      { path: 'serviceOrganization', name: 'serviceOrganization', component: ServiceOrganization },
      { path: 'expert', name: 'expert', component: expert },
      { path: 'industryActivity', name: 'industryActivity', component: IndustryActivity },
      { path: 'industryInfo', name: 'industryInfo', component: IndustryInfo },
      { path: 'businessList', component: BusinessList },
      { path: 'terraceList', component: TerraceList },
      { path: 'gardenList', component: GardenList },
      { path: 'gardenListDetails', component: GardenListDetails },
      { path: 'regulations', component: regulations },
      { path: 'Idynamics', component: Idynamics },
      { path: 'specialList', component: SpecialList },
      { path: 'specialListDetails', component: SpecialListDetails }
    ]
  }, {
    path: '/unionLayout',
    name: 'unionLayout',
    component: UnionLayout,
    children: [
      { path: '', name: 'unionHome', component: UnionHome },
      { path: 'joinUnion', name: 'joinUnion', component: JoinUnion }
    ]
  }
  ]
})

export default router



