          <div class="demand-detail-card large">
            <h5>需求详情</h5>
            <div class="card-content">
              <p class="special">在网络传媒越来越发达的今天，路灯杆触电事故也引发了广泛的讨论，有的媒体竟然发出这样的感叹“危险的电线杆！“道路照明给我们出行生活带来极大的便利，是我们生活中必不可少的一部分。但是我们常常会发现，在非常需要照明的恶劣天气，路灯却不亮。这不能怪管理部门，管理部门也是出于一片好心，担心会引发漏电事故，对人们造成伤害，因而在恶劣环境下只好断电。“安全用电，可靠供电”是道路照明的基础。2014年底，因某地市路灯管理部门的要求，秉岳电气开始关注路灯杆防漏电。通过大量的调查研究，秉岳电气发现，路灯杆漏电的主要原因是灯杆内电缆连接处理不当造成的，占90%以上的因素。基于我们的社会责任，我们研发了路灯杆防漏电连接器。全国招商 路灯新产品，电力行业新产品、新商机，让城市更安全。</p>
